"use client";

import { useState, useEffect } from 'react';
import { Search, ShoppingBag, Menu, X, User } from 'lucide-react';
import Link from 'next/link';
import { motion, useScroll, useMotionValueEvent } from 'framer-motion';

export default function Header() {
    const [isScrolled, setIsScrolled] = useState(false);
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const { scrollY } = useScroll();

    useMotionValueEvent(scrollY, "change", (latest) => {
        if (latest > 50) {
            setIsScrolled(true);
        } else {
            setIsScrolled(false);
        }
    });

    return (
        <header
            className={`fixed top-0 left-0 right-0 z-50 transition-colors duration-300 ${isScrolled ? 'bg-[#3F3127] shadow-md py-4' : 'bg-transparent py-6'
                }`}
        >
            <div className="max-w-7xl mx-auto px-6 flex items-center justify-between">
                {/* Logo */}
                <Link href="/" className="text-2xl font-bold text-white tracking-widest">
                    BLANC.
                </Link>

                {/* Desktop Navigation */}
                <nav className="hidden md:flex items-center gap-10">
                    {['SOFA', 'CHAIR', 'TABLE', 'LAMP', 'DECOR'].map((item) => (
                        <Link
                            key={item}
                            href="/"
                            className="text-white/80 hover:text-white text-sm font-medium tracking-wide transition duration-200"
                        >
                            {item}
                        </Link>
                    ))}
                </nav>

                {/* Actions */}
                <div className="flex items-center gap-6 text-white">
                    <button className="hover:opacity-80 transition">
                        <Search size={20} />
                    </button>
                    <button className="hidden md:block hover:opacity-80 transition">
                        <User size={20} />
                    </button>
                    <button className="relative hover:opacity-80 transition">
                        <ShoppingBag size={20} />
                        <span className="absolute -top-1 -right-1 bg-white text-[#3F3127] text-[10px] font-bold w-3.5 h-3.5 flex items-center justify-center rounded-full">
                            2
                        </span>
                    </button>

                    {/* Mobile Menu Toggle */}
                    <button
                        className="md:hidden hover:opacity-80 transition"
                        onClick={() => setIsMenuOpen(!isMenuOpen)}
                    >
                        {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
                    </button>
                </div>
            </div>

            {/* Mobile Menu Dropdown */}
            {isMenuOpen && (
                <motion.div
                    initial={{ opacity: 0, y: -20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="absolute top-full left-0 right-0 bg-[#3F3127] border-t border-white/10 p-6 md:hidden flex flex-col gap-4 shadow-xl"
                >
                    {['SOFA', 'CHAIR', 'TABLE', 'LAMP', 'DECOR'].map((item) => (
                        <Link
                            key={item}
                            href="/"
                            className="text-lg font-medium text-white/90 py-3 border-b border-white/10 last:border-none"
                            onClick={() => setIsMenuOpen(false)}
                        >
                            {item}
                        </Link>
                    ))}
                </motion.div>
            )}
        </header>
    );
}
